body,
body * {
  font-family: 'Bangers', cursive;
  letter-spacing: 1px; }

@-webkit-keyframes zoombg {
  0% {
    opacity: 0.10;
    background-size: 100%; }
  50% {
    opacity: 1;
    background-size: 130%; }
  100% {
    opacity: 0.10;
    background-size: 160%; } }

@keyframes zoombg {
  0% {
    opacity: 0.10;
    background-size: 100%; }
  50% {
    opacity: 1;
    background-size: 130%; }
  100% {
    opacity: 0.10;
    background-size: 160%; } }

@media (max-width: 768px) {
  .mainbox {
    min-height: 250px; }
  .mobilecrop {
    margin-left: 8px;
    margin-right: 8px; }
  .mobilecrop .projects {
    margin-bottom: 0; } }

.Main {
  text-align: left;
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
  min-height: 645px; }

.Main::after {
  content: "";
  background-image: url("./assets/bgridopacity.png");
  background-position: center center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1; }

@media screen and (min-width: 1024px) {
  .Main::after {
    animation: zoombg 16s linear infinite;
    -webkit-animation: zoombg 16s linear infinite; } }

.Main-header {
  background-color: #fff;
  padding: 40px 20px 20px;
  color: #000;
  border-bottom: 5px solid #ccc; }

h1.Main-title {
  cursor: pointer;
  font-size: 1.8rem;
  text-decoration: none; }
  h1.Main-title a {
    color: #0078d7;
    text-decoration: none; }

.title-subheading {
  font-family: 'Oswald', sans-serif;
  font-size: 1em; }

.Main-title,
.mainbox h2 {
  font-size: 1.5em;
  color: #0078d7; }

.Main-header .Nav {
  display: inline-flex;
  text-align: right;
  color: #0078d7;
  padding: 0; }
  .Main-header .Nav li {
    display: inline-block;
    font-family: 'Bangers', cursive;
    padding: 0 8px 0 0; }
  .Main-header .Nav li a {
    text-decoration: none;
    display: block;
    padding-top: 5px;
    border-bottom: 5px solid #fff;
    padding-bottom: 3px;
    font-size: 1.2rem;
    color: #0078d7; }
  .Main-header .Nav li a:hover {
    border-bottom: 5px solid #000000;
    cursor: pointer; }

.Main-intro {
  font-size: large; }

.mainbox {
  min-height: 280px;
  margin: 40px 0;
  padding: 20px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc; }
  .mainbox p {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.3em; }
  .mainbox a {
    color: #0078d7; }
  .mainbox .infoList {
    padding: 10px;
    margin: 10px; }
  .mainbox .infoList li {
    font-size: 1.4em;
    list-style: none;
    color: #484848;
    padding-left: 8px;
    border-left: 3px solid #ccc;
    margin-bottom: 10px;
    font-family: 'Oswald', sans-serif;
    font-size: 1em; }

@media (min-width: 992px) {
  .mainbox {
    min-height: 260px; } }

.detailbox {
  margin: 40px 0;
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 20px; }
  .detailbox h1 {
    color: #0078d7; }
  .detailbox p {
    font-family: 'Oswald', sans-serif;
    font-size: 1.5em; }
  .detailbox a {
    color: #0078d7; }
  .detailbox .about-format {
    font-family: 'Oswald', sans-serif; }
  .detailbox .projects-code > .row {
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    margin: 20px 10px;
    padding-bottom: 20px; }
  .detailbox .projects-code p {
    font-size: 24px; }
  .detailbox .projects-code img {
    border: 1px solid #ccc; }
  .detailbox .projects-code a {
    display: block;
    cursor: pointer; }

.project-entry {
  padding-top: 20px;
  padding-bottom: 20px; }

footer {
  border-top: 5px solid #ccc;
  padding: 20px 0; }
  footer i {
    margin: 0 4px;
    font-size: 24px; }
  footer .fa-react {
    color: #00d8ff; }

.closex {
  cursor: pointer; }

.white-bg {
  background-color: #fff; }

.fa-html5 {
  color: #e34f26; }

.fa-css3-alt {
  color: #e34f26; }

.fa-js-square {
  color: #f7df1e; }

.fa-node {
  color: #6cc24a; }

.fa-database {
  color: #44883e; }

.fa-apple {
  color: #000000; }

.fa-windows {
  color: #0078d7; }

